<template>
    <transition :name="transition">
        <div class="modal-mask" @click="close" v-show="show">
            <div class="modal-container" :class="[ modalClass ]">
                <slot></slot>
            </div>
        </div>
    </transition>
</template>

<script>
    export default{
        template: '#modal-template',
        props: {
            show: Boolean,
            onClose: Function,
            transition: {
                type: String,
                default: 'scale'
            },
            modalClass: String
        },
        methods: {
            close: function () {
                this.onClose()
            }
        },
        mounted: function () {
            document.addEventListener('keydown', function (e) {
                if (this.show && e.keyCode === 27) {
                    this.onClose()
                }
            }.bind(this))
        }
    }

</script>



